<template>
  <div class="player">
    <div id="player"></div>
    <div id="chapter_list">
      <el-tree :data="course_info.children" @node-click="handleNodeClick"></el-tree>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Player",
        data() {
            return {
                token: "",
                course_info: {},
                vid: 0,
            }
        },
        methods: {
            get_course_info(course_id) {
                // 
                this.$axios.get(`/courses/chapters2/`, {
                    params: {"course": course_id},
                    headers: {
                        "Authorization": "jwt " + this.token,
                    }
                }).then(response => {
                    console.log(response.data);
                    this.course_info = response.data;
                })
            },
            handleNodeClick(data) {
                // 
                if (data.lesson_type && data.lesson_type === 2) {
                    this.vid = data.lesson_link
                }
            },
            player_video(vid) {
                // 
                let self = this;
                let player = polyvPlayer({
                    wrap: '#player',
                    width: document.documentElement.clientWidth * 0.8, // 
                    height: document.documentElement.clientHeight, // 
                    forceH5: true,
                    vid: vid,
                    code: localStorage.user_name, // 
                    // 
                    playsafe: function (vid, next) { // token
                        self.$axios.get(`/courses/play/`, {
                            params: {
                                vid: vid,
                            },
                            headers: {
                                "Authorization": "jwt " + self.token,
                            }
                        }).then(function (response) {
                            // token
                            next(response.data);
                        })
                    }
                });
            }
        },
        watch: {  // 
            vid() {
                // ,vid,
                this.player_video(this.vid);
            }
        },
        created() {
            // 
            this.token = this.$settings.checkoutUserLogin(this);
            if (this.token) {
                let user_name = localStorage.user_name || sessionStorage.user_name;
                let course_id = localStorage.course_id;
                // 
                this.get_course_info(course_id);
            }
        }

    }
</script>

<style scoped>
  #player {
    width: 80%;
    float: left;
  }

  #chapter_list {
    width: 20%;
    float: right;
  }
</style>



